import React, { useState } from 'react';
import { Controller, Scene } from 'react-scrollmagic';
import { Tween, Timeline } from 'react-gsap';
import logo from './assets/basic/logo.png';
import lobbyTitle from './assets/pages/lobby/title-lobby.png';
import arrowRigth from './assets/icons/arrow/right-black.png';
import jsonData from './json/index.json';

function App() {
  let windowHeight = document.body.clientHeight;

  // 
  const [menuStatus, setMenuStatus] = useState(false);
  function openMenu() {
    setMenuStatus(!menuStatus);
    console.log('Menu Changed:', menuStatus);
  }

  // 
  const [navStatus, setNavStatus] = useState(0);
  function changeNav(floor) {
    setMenuStatus(floor);
    console.log('Nav Changed:', floor);
  }

  const [lobbyInfo, setLobbyInfo] = useState({
    index: 0,
    title:
      '<p><span></span></p><p><span> </span></p>',
    content:
      ' 20  100  65 ',
    cta: '',
    href: '#'
  });
  function changeInfo(i) {
    setLobbyInfo(jsonData.lobbyInfo[i]);
  }

  return (
    <div className="App">
      <nav className="nav">
        <a
          className={
            menuStatus ? 'nav__trigger nav__trigger--active' : 'nav__trigger'
          }
          href="javascript:;"
          onClick={openMenu}
        >
          <span></span>
          <span></span>
        </a>
        <div
          className={
            menuStatus
              ? 'nav__container nav__container--active'
              : 'nav__container'
          }
        >
          <a
            className={
              navStatus == 0 ? 'nav__link nav__link--active' : 'nav__link'
            }
            href="#lobby"
          >
            <span></span>
            <p>
              Lobby
              <br />
              
            </p>
          </a>
          <a
            className={
              navStatus == 1 ? 'nav__link nav__link--active' : 'nav__link'
            }
            href="#"
          >
            <span></span>
            <p>
              1st Floor
              <br />
              
            </p>
          </a>
          <a
            className={
              navStatus == 2 ? 'nav__link nav__link--active' : 'nav__link'
            }
            href="#"
          >
            <span></span>
            <p>
              2nd Floor
              <br />
              
            </p>
          </a>
          <a
            className={
              navStatus == 3 ? 'nav__link nav__link--active' : 'nav__link'
            }
            href="#"
          >
            <span></span>
            <p>
              3rd Floor
              <br />
              
            </p>
          </a>
        </div>
      </nav>
      <Controller>
        <Scene duration={300}>
          {progress => (
            <Tween
              to={{
                top: '0px',
                left: '0px',
                width: '10.625em',
                height: '9.8em'
              }}
              ease="Strong.easeOut"
              totalProgress={progress}
              paused
            >
              <div className="logo">
                <img src={logo} alt="" />
              </div>
              {/* <div className="tween">Pin Test</div> */}
            </Tween>
          )}
        </Scene>
      </Controller>
      <Controller globalSceneOptions={{ triggerHook: 'onLeave' }}>
        <Scene pin>
          <div className="panel home" id="home">
            <div className="home__wrapper">
              <a href="#lobby" className="home__entry"></a>
              <div className="info">
                <div className="info__title">
                  <p> </p>
                </div>
                <div className="info__container">
                  <a href="#" className="info__link">
                    <p> vs. </p>
                    <span>
                      #
                      <br /> #
                    </span>
                  </a>
                  <a href="#" className="info__link">
                    <p></p>
                    <span># # # #</span>
                  </a>
                  <a href="#" className="info__link">
                    <p></p>
                    <span># # #</span>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </Scene>
        <Scene pin>
          <div className="panel lobby" id="lobby">
            <div className="lobby__wrapper">
              <div className="info">
                <div className="info__container">
                  <div className="info__index">
                    <p>Question #{lobbyInfo.index + 1}</p>
                  </div>
                  <div
                    className="info__title"
                    dangerouslySetInnerHTML={{ __html: lobbyInfo.title }}
                  ></div>
                  <div className="info__content">
                    <p>{lobbyInfo.content}</p>
                  </div>
                  <a href="#" className="info__cta">
                    <p>{lobbyInfo.cta}</p>
                    <img src={arrowRigth} alt="" />
                  </a>
                </div>
              </div>
              <div
                className="lobby__question lobby__question--1"
                onClick={() => changeInfo(0)}
              ></div>
              <div
                className="lobby__question lobby__question--2"
                onClick={() => changeInfo(1)}
              ></div>
              <div
                className="lobby__question lobby__question--3"
                onClick={() => changeInfo(2)}
              ></div>
              <div
                className="lobby__question lobby__question--4"
                onClick={() => changeInfo(3)}
              ></div>
            </div>
            <div className="lobby__title">
              <img src={lobbyTitle} alt="" />
            </div>
          </div>
        </Scene>
        <Scene pin>
          <div className="panel green">
            <span>Panel</span>
          </div>
        </Scene>
        <Scene pin>
          <div className="panel bordeaux">
            <span>Panel</span>
          </div>
        </Scene>
      </Controller>
    </div>
  );
}

export default App;
